
body {
  background-image: url(https://i.pinimg.com/originals/52/d4/8a/52d48a2e1cb69da394a68072ce096169.jpg);
  background-size: cover;
}

.navbar {
  z-index: -1;
}

.google-map {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: #fff;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}

.signup {
  max-width: 500px;
  margin-top: 60px;
}
.signup h2 {
  font-size: 2.4em;
}
.signup field {
  margin-bottom: 16px;
}

.login {
  max-width: 500px;
  margin-top: 60px;
}
.login .h2 {
  font-size: 2.4em;
}
.login .field {
  margin-bottom: 16px;
}
.card-panel {
  background: blue lighten-3;
}

.view-profile .card {
  padding: 20px;
  margin-top: 60px;
}
.view-profile .h2 {
  font-size: 2.4em;
  margin-top: 0px;
}
.view-profile .li {
  padding: 10px;
  border-bottom: 1px solid #eee;
}
